import React, { Component } from 'react';
import { BrowserRouter as Router, Route, NavLink, Link, Switch } from 'react-router-dom';
import helper from '../public/helper';
import DialogNormal from '../dialog/normal';
import ToastNormal from '../toast/normal';
import { Style } from './resource';

class CommonUI extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  
  handleShowDialog(type, event) {
  	let dialog = {
  		display: "block",
  		title: ""
  	};
  	switch(type){
  		case 1:
  			dialog.closable = true;
  			break;
  		case 2:
  			dialog.content = "";
				dialog.buttons = [{
					text: "Okay",
					className: "btnSure",
					handleClick: function(){
						this.hide();
					}
				}];
  			break;
  		case 3:
  			dialog.content = "";
				dialog.buttons = [{
					text: "Okay",
					className: "btnSure",
					handleClick: function(){
						this.hide();
					}
				}, {
					text: "Cancel",
					className: "btnSure",
					handleClick: function(){
						this.hide();
					}
				}];
  			break;
  	}
		this.setState({ dialog });
  }
  
  handleHideDialog() {
  	this.setState({
			dialog: {
    		display: "none"
    	}
		});	
	}
	
  handleShowToast(type, event) {
  	let toast = {
  		display: "block",
  		content: ""
  	};
  	switch(type){
  		case 1:
  			toast.closable = true;
  			toast.timer = -1;
  			break;
  		case 2:
  			toast.timer = 3000;
  			break;
  	}
		this.setState({ toast });
	}
	
  handleHideToast() {
  	this.setState({
			toast: {
    		display: "none"
    	}
		});	
	}
	
  render() {
  	let _propsDialog = this.state.dialog;
  	let _propsToast = this.state.toast;
  	let {location, history} = this.props;
  	let props = {location, history};
		let myLocation = Object.assign({}, location);

    return (
      <div className="common-ui default">
      	<div>
	        <h2 style={{margin: "20px auto 10px"}}>Dialog</h2>
	        <DialogNormal {..._propsDialog} onHide={this.handleHideDialog.bind(this)} /> 
	        <div>
		        <button onClick={this.handleShowDialog.bind(this, 1)}></button>
		        <button onClick={this.handleShowDialog.bind(this, 2)}></button>
		        <button onClick={this.handleShowDialog.bind(this, 3)}></button>
	        </div>
        </div>
      	<div>
	        <h2 style={{margin: "20px auto 10px"}}>Toast</h2>
	        <ToastNormal {..._propsToast} onHide={this.handleHideToast.bind(this)} /> 
	        <div>
		        <button onClick={this.handleShowToast.bind(this, 1)}></button>
		        <button onClick={this.handleShowToast.bind(this, 2)}></button>
	        </div>
        </div>
        <div>
	        <h2 style={{margin: "20px auto 10px"}}></h2>
	        <div></div>
        </div>
        <div>
	        <h2 style={{margin: "20px auto 10px"}}></h2>
	        <div></div>
        </div>
      </div>
    );
  }
}

export default CommonUI;



